* {
  margin: 0px;
  padding: 0px;
  font-family: Helvetica, Arial, sans-serif;
  list-style: none;
  box-sizing: border-box;
}

body {
  padding: 60px;
}

*::before, *::after {
  box-sizing: border-box;
}

input {
  font-size: 1.3rem;
  width: 100%;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}